@font-face {
	font-display: auto;
	font-family: Font-Awesome-Brands;
	font-style: normal;
	font-weight: 400;
	src: url(../fonts/fa-brands-400.woff2) format("woff2"), url(../fonts/fa-brands-400.woff) format("woff"), url(../fonts/fa-brands-400.ttf) format("truetype");
}

@font-face {
	font-display: auto;
	font-family: Font-Awesome-Solid;
	font-style: normal;
	font-weight: 900;
	src: url(../fonts/fa-solid-900.woff2) format("woff2"), url(../fonts/fa-solid-900.woff) format("woff"), url(../fonts/fa-solid-900.ttf) format("truetype");
}

a.icon {
	color: var(--color-grass, forestgreen);
	text-decoration: none;
}

html {
	font-family: sans-serif;
	font-size: var(--font-size-tiny, 12px);
	line-height: 1.5;
}

.icon::before {
	font-family: Font-Awesome-Solid;
}

.icon-github::before {
	font-family: Font-Awesome-Brands;
}

.punch-card {
	color: var(--color-greyn, darkgrey);
	font-family: Consolas;
	font-variant-numeric: oldstyle-nums;
	letter-spacing: var(--letter-spacing-punch-card, 1rem);
	user-select: none;
}

.punch-card::before {
	letter-spacing: initial;
	text-align: right;
}

.punch-card a {
	color: inherit;
	text-decoration: none;
	transition-duration: var(--transition-duration-default, 1s);
	transition-timing-function: var(--transition-timing-function-default, ease-in-out);
	transition-property: color;
}

.punch-card a:focus,
.punch-card a:focus .punch-hole,
.punch-card a:hover,
.punch-card a:hover .punch-hole {
	color: initial;
	font-weight: var(--font-weight-bolder, 900);
	letter-spacing: initial;
}

.punch-card .punch-hole {
	color: transparent;
	letter-spacing: initial;
}

.punch-card-row:hover a,
.punch-card-row:hover a .punch-hole {
	color: initial;
	font-weight: var(--font-weight-bolder, 900);
	letter-spacing: initial;
	user-select: text;
}

@media (min-width: 1366px) {
	html {
		font-size: var(--font-size-small, 14px);
	}
}

@media (min-width: 1920px) {
	html {
		font-size: var(--font-size-medium, 16px);
	}
}

@media (min-width: 2560px) {
	html {
		font-size: var(--font-size-large, 18px);
	}
}

@media (min-width: 3840px) {
	html {
		font-size: var(--font-size-huge, 20px);
	}
}
